import {observable} from 'mobx';

// custom store
const store = {};
export const setModel = (model, val) => {
  store[model] = val;
};
export const getModel = (model) => {
  return store[model];
};

// custom models
export const newTaskModel = observable({
  title:'New Task',
  color:'Yellow',
  priority: 0
});

export const setDefaultTask = () => {
  newTaskModel.title = 'New Task';
  newTaskModel.color = 'Yellow';
  newTaskModel.priority = 0;
};






